body {
  color: #333;
  overflow-x: hidden;
  width: 100%;
}

a {
  color: #900;
  font-weight: 600;
  text-decoration: none;
  transition: 0.3s;
}

a:hover,
a:focus {
  color: #c00;
  text-decoration: underline;
}

::selection {
  background: #900;
  color: #fff;
}

.container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  max-width: 1140px;
}

@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.loading::after {
  animation: rotate 1s infinite linear;
  border: 6px solid #c00;
  border-radius: 36px;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 36px;
  margin: 0 auto;
  width: 36px;
}

.btn {
  background: #900;
  border-radius: 0.5rem;
  color: #fff;
  display: inline-block;
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  text-shadow: 0 -1px 0 rgba(0, 0, 16, 0.5);
}

.btn:hover,
.btn:focus {
  background: #c00;
  box-shadow: inset 0 0 20px #900;
  color: #fff;
  text-decoration: none;
}

.btn--nav::before {
  content: "";
  padding-right: 0.25rem;
}

.title {
