.header {
  background: #fff;
  box-shadow: 0 -3px 3px rgba(0, 0, 16, 0.5);
}

.header__title {
  color: #900;
  font: bold 42px Verdana, Geneva, sans-serif;
  height: 40px;
  letter-spacing: -3px;
  overflow: hidden;
  text-shadow: 1px -1px 0 rgba(0, 0, 16, 0.5);
}

@include media-breakpoint-up(md) {
  .header__title {
    font-size: 80px;
    height: 75px;
  }
}

.header__nav-wrapper {
  background: #900;
  background-image: linear-gradient(#900, #600);
  box-shadow: 0 3px 3px rgba(0, 0, 16, 0.5);
  padding: $spacer 0;
}

@include media-breakpoint-up(sm) {
  .header__nav-wrapper {
    padding: 0;
  }
}

.header__nav {
  clear: both;
  max-height: 0;
  overflow: hidden;
  transition: 0.3s;
}

@include media-breakpoint-up(sm) {
  .header__nav {
    max-height: initial;
  }
}

.header__nav.active {
  max-height: 250px;
}

.header__nav-link {
  $font-size: 18px;

  color: #fff;
  display: block;
  font-family: "Lato", arial, serif;
  font-size: $font-size;
  font-weight: 400;
  line-height: $font-size * 3;
  text-shadow: 0 -1px 0 rgba(0, 0, 16, 0.5);
}

.header__nav-link:hover,
.header__nav-link:focus,
.header__nav-link.active {
  background: #900;
  color: #fff;
  text-decoration: none;
}
